body {
    margin: 0px;
    overflow: hidden;
    user-select: none;
}

.loader {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: fixed;
    z-index: 2;
    background-color: #111;
    line-height: 100vh;
    text-align: center;
    font-size: 40px;
    color: #999;
    transition: opacity 0.3s;
}