.arimac_logo {
    position: fixed;
    width: 65px;
    height: 73px;
    z-index: 10;
    top: calc(100vh - 90px);
    left: 15px;
    background-size: contain;
}

.arimac_logo_white {
    background-image: url("arimac_white.png");
}

.arimac_logo_black {
    background-image: url("arimac_black.png");
}

@media (orientation: portrait) {
    .arimac_logo {
        width: 40px;
        height: 45px;
        top: calc(100vh - 120px);
        left: 20px;
    }
}